<template>
  <div class="article-list" ref="list" @scroll="rememberScroll">
      <van-pull-refresh v-model="isRefreshing" @refresh="onRefresh">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
    <article-item :article="article" v-for="article in articleList" :key="article.art_id" />
    </van-list>
      </van-pull-refresh>
  </div>
</template>

<script>
import articleItem from './article-item.vue'
import { getArticleListByCID } from '@/api/home'
export default {
  props: ['cid'],
  components: {
    articleItem
  },
  // 这是生命周期函数(keep-alive)，使用keep-alive才有这个函数
  activated () {
    // 当你重新回到首页页面时，将this.scrollTop赋值给this.$refs.list.scrollTop
    this.$refs.list.scrollTop = this.scrollTop
  },
  methods: {
    rememberScroll () {
      // 记录滚动的位置
    //   console.log(this.$refs.list.scrollTop)
      this.scrollTop = this.$refs.list.scrollTop
    },
    async onRefresh () {
      // 刷新页面的函数
      this.isRefreshing = true
      //   console.log('下拉了')
      const [err, data] = await getArticleListByCID(this.cid, +new Date())
      if (err) return this.$toast.fail(err)
      this.articleList = data.data.data.results
      this.isRefreshing = false
    },
    async onLoad () {
      this.loading = true
      // 初始化时，因为没有内容，所以默认触底，所以自动执行
      //   console.log('触底自动执行')
      const [err, data] = await getArticleListByCID(this.cid, this.timeStamp)
      if (err) return this.$toast.fail(err)

      this.articleList.push(...data.data.data.results)

      if (!data.data.data.pre_timestamp) {
        // 如果pre_timestamp为空，说明没有数据，finished文本将显示
        this.finished = true
      } else {
        // 为下一次请求做准备，为请求更多数据做准备
        this.timeStamp = data.data.data.pre_timestamp
      }

      this.loading = false
    }
  },
  data () {
    return {
      isRefreshing: false,
      loading: false,
      finished: false,
      articleList: [],
      timeStamp: +new Date(),
      scrollTop: 0
    }
  },
  created () {
    // const [err, data] = await getArticleListByCID(this.cid, this.timeStamp)
    // if (err) return this.$toast.fail(err)
    // console.log(data)
    // this.articleList = data.data.data.results
    // this.timeStamp = data.data.data.pre_timestamp
  }
}
</script>

<style scoped>
.article-list {
  height: 100%;
  overflow-y: auto;
  padding: 0 16px;
}
</style>
